import test from 'ava';
import cards from './cards';

test('cards reducer', (t) => {
	const oldState = [];
	const actions = [{
		type: 'ADD_CARD',
		id: 1, 
		name: 'Johee',
		photo: 'johee.jpg',
	}, {
		type: 'ADD_CARD',
		id: 2,
		name: 'Ken',
		photo: 'poopybutthole.jpg',
	}];
	const newState = actions.reduce(cards, oldState);
	t.deepEqual(newState, [{ id: 1, name: 'Johee', photo: 'johee.jpg' }, { id: 2, name: 'Ken', photo: 'poopybutthole.jpg' }]);
});
